<template>
  <div class="app">
    <div class="loginwrop">
      <van-cell-group>
        <h1 class="title">登录</h1>
        <van-field
          v-model="username"
          required
          clearable
          label="用户名"
          placeholder="请输入用户名"
          @input="usernameval"
          left-icon="contact"
          class="inp"
        />
        <van-field
          v-model="password"
          @input="passwordval"
          class="inp"
          type="password"
          label="密码"
          placeholder="请输入密码"
          required
          clearable
          left-icon="bag-o"
        />
        <van-button
          @click="login"
          type="primary"
          size="large"
          hairline
          :loading="loadingstatus"
          loading-text="登录..."
          class="btn"
        >登录</van-button>
      </van-cell-group>
    </div>
  </div>
</template>
<script>
import axios from "../axios/axios";
export default {
  name: "login",
  data() {
    return {
      username: "",
      password: "",
      loadingstatus: false
    };
  },
  mounted() {},
  methods: {
    // 登录
    login() {
      if (!this.username.trim() || !this.password.trim()){
          this.$toast.fail('参数不能为空');
          return
      }
      let param = {username: this.username,password: this.password};
      this.loadingstatus = true
      axios.post("login",param).then((res) => {
        console.log(res);
        if (res.data.code){
          let token = res.data.data.token
          if (token){
            localStorage.setItem('token',token)
            this.$router.push({path:'/list'});
          }else{
            this.$toast.fail('token为空');
             this.loadingstatus = false
          }
          
        }else{
          this.$toast.fail(res.data.msg?res.data.msg:'参数有误');
          this.loadingstatus = false
        }
      }).catch((err)=>{
        this.$toast.fail('登录失败');
        console.log(err)
      })
    },
    usernameval(val) {
      this.username = val;
    },
    passwordval(val) {
      this.password = val;
    }
  }
};
</script>

<style  scoped>
.app {
  width: 100%;
  height: 100%;
  display: flex;
  /* background-image: linear-gradient(to bottom, #cfcfcf, #ffffff); */
  background:white
}
.title {
  text-align: center;
}
.loginwrop {
  width: 90vw;
  height: 340px;
  margin: auto;
  background: #f3f1f1;
  /* padding: 20px 0; */
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
}
.van-hairline--top-bottom::after {
  border: 0;
}
.van-cell-group {
  background: #f3f1f1;
  margin-top: 10px;
  width: 95%;
  margin: 10px auto;
}
.van-cell:not(:last-child)::after {
  border-bottom: 0;
}
.inp {
  background: rgba(117, 116, 116, 0.199);
  margin: 25px 0;
}
.btn {
  margin-top: 30px;
}
</style>




